<template>
  <!--  -->
  <el-dialog class="app_detail_update_appinfo" title="" :visible.sync="dialogVisible.dialogVisible" :before-close="beforeClose" width="30%">
    <!-- 2 -->
    <div class="app_cover">
      <div class="app_cover_content">
        <el-input type="textarea" :rows="6" resize="none" placeholder="" v-model="dialogVisible.description"></el-input>
        <!-- <textarea placeholder="" class="app_cover_content_change_textarea" name="" id="" cols="30" rows="10"></textarea> -->
      </div>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="beforeClose"> </el-button>
      <el-button type="primary" @click="submitChange" :loading="dialogVisible.loadding"> </el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  props: {
    dialogVisible: { // 
      type: Object,
      required: true
    }
  },
  data() {
    return {
      typeList: []
    }
  },
  methods: {
    // 
    submitChange() {
      this.dialogVisible.loadding = true;
      this.dialogVisible.isUpload = true;
      // 
      this.$emit('submit');
    },

    /**
     *  
     *  
     *   
     */
    beforeClose() {
      console.log('')
      if (this.dialogVisible.isUpload) {
        console.log('');
        return;
      }
      console.log('');
      this.dialogVisible.dialogVisible = false;
    }
  }
}
</script>


<style>
/*  start */
.app_cover_content {
  padding: 0 20px;
}
.app_cover_content_change_textarea {
  box-sizing: border-box;
  width: 100%;
  height: 100px;
  margin: 30px 0 100px 0;
  padding: 10px 6px;
  border: 1px solid #e6e6e6;
  font-size: 14px;
  resize: none;
  outline: none;
  color: #333333;
}
</style>
