<template>
  <!--  -->
  <el-dialog class="app_detail_update_appinfo" title="" :visible.sync="dialogVisible.dialogVisible" :before-close="beforeClose" width="30%">
    <!-- 2 -->
    <div class="app_cover">
      <div class="app_cover_content">
        <div class="app_cover_content_logo">
          <p class="app_cover_content_l_des">logo</p>
          <div class="app_cover_content_logowrap">
            <span>LOGO</span>
            <img :src="dialogVisible.logoUrl" alt="">
            <input type="file" @input="logoChange">
          </div>
          <p class="app_cover_content_des">750*7501MJPGPNG</p>
        </div>
        <div class="app_cover_content_name">
          <p class="app_cover_content_l_des"></p>
          <input type="text" v-model="dialogVisible.appName" placeholder="" />
        </div>
        <div class="app_cover_content_type">
          <p class="app_cover_content_l_des"></p>
          <div class="app_cover_content_type_wrap">
            <el-radio v-model="dialogVisible.typeid" v-for="item in typeList" :key="item._id" :label="item._id">{{item._name}}</el-radio>
            <!-- <el-radio v-model="dialogVisible.typeid" label="1"></el-radio>
            <el-radio v-model="dialogVisible.typeid" label="2"></el-radio> -->
          </div>
        </div>
      </div>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="beforeClose"> </el-button>
      <el-button type="primary" @click="submitChange" :loading="dialogVisible.loadding"> </el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  props: {
    dialogVisible: { // 
      type: Object,
      required: true
    }
  },
  data() {
    return {
      typeList: []
    }
  },
  methods: {
    // 
    logoChange(e) {
      console.log('');
      console.log(e);
      console.log('');
      console.log(e.target.files);
      this.dialogVisible.imgFile = e.target.files[0];
      // url
      this.dialogVisible.logoUrl = this.$zero.fileToBlobUrl(e.target.files[0]);
    },

    // 
    submitChange() {

      this.dialogVisible.loadding = true;
      this.dialogVisible.isUpload = true;

      // 
      this.$emit('submit');
    },

    /**
     *  
     *  
     *   
     */
    beforeClose() {
      console.log('')
      if (this.dialogVisible.isUpload) {
        console.log('');
        return;
      }
      console.log('');
      this.dialogVisible.dialogVisible = false;
    },

    // 
    async getType() {
      let company_id = this.$zero.getCompany_id();
      let typeResult = await this.$api.application.getTypeList({
        company_id: company_id
      });
      console.log('');
      console.log(typeResult);
      if (typeResult.statusCode === 75500) {
        return this.$zero.ToastError('');
      }
      if (typeResult.statusCode !== 75200) {
        return this.$zero.ToastError(`:${typeResult.statusCode}`);
      }

      // 
      console.log('');
      console.log(typeResult);
      this.typeList = typeResult.types;
    },
  },

  created() {
    console.log('');
    console.log(this.dialogVisible);
    this.getType();
  }
}
</script>


<style>
/* 1 */
.app_detail_update_appinfo .el-dialog__body {
  padding: 0 20px;
}

.app_detail_update_appinfo .el-dialog__footer {
  text-align: center;
}

.app_cover_content {
  padding: 0 20px;
}

.app_cover_content_logo {
  display: flex;
  padding-top: 35px;
}

.app_cover_content_l_des {
  width: 120px;
  color: #999999;
}

.app_cover_content_logowrap {
  position: relative;
  width: 60px;
  height: 60px;
  border: 1px solid #e6e6e6;
  text-align: center;
  line-height: 60px;
}

.app_cover_content_logowrap input {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  cursor: pointer;
}

.app_cover_content_logowrap img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.app_cover_content_des {
  line-height: 60px;
  margin-left: 10px;
  color: #999999;
}

.app_cover_content_name {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.app_cover_content_name input {
  box-sizing: border-box;
  width: 350px;
  height: 35px;
  padding-left: 8px;
  border: 1px solid #e6e6e6;
}

.app_cover_content_type {
  display: flex;
  align-items: center;
  margin-top: 20px;
  padding-bottom: 40px;
}

/* 1 */
</style>
