<template>
  <div class="appmanage_detail">
    <!--  -->
    <div class="c_content_header flex flex-item">
      <div class="flex flex-item">
        <i @click="backBtn" class="el-icon-arrow-left"></i>
        <p class="cch_con"></p>
      </div>
      <div class="appd_t_btn">
        <div class="appd_t_btn_item" v-if="appDetail.status === 0 || appDetail.status === 2">
          <el-button type="primary" @click="publishApp"></el-button>
          <el-button type="danger" @click="delApp"></el-button>
        </div>

        <div class="appd_t_btn_item" v-if="appDetail.status === 1">
          <el-button type="primary" @click="updateApp"></el-button>
          <el-button type="danger" @click="delApp"></el-button>
        </div>

      </div>
    </div>

    <div class="appmanage_detail_wrap" v-if="response.isResponse && response.error === false">

      <div class="appd_detail">
        <div class="appd_detail_content">

          <!--  -->
          <div class="appd_detail_content_header clearfix" v-if="appDetail">
            <div class="appd_detail_content_header_l fl">
              <div class="appd_d_ch_logo">
                <!-- <img src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAIEAAACBCAMAAADQfiliAAAAA1BMVEWJz/C4QSRIAAAAJklEQVR4nO3BAQEAAACCIP+vbkhAAQAAAAAAAAAAAAAAAAAAALwbQYIAAVCVNwcAAAAASUVORK5CYII=" alt=""> -->
                <img :src="appDetail.icon" alt="">
              </div>
              <div class="appd_d_ch_des">
                <div class="appd_d_ch_title clearfix">
                  <p class="fl">{{appDetail._name}}</p>
                  <!-- <span class="fl">()</span> -->
                  <img @click="editAppInfo" class="edit_icon" :src="require('../../assets/img/appManage/edit.png')" alt="">
                </div>
                <div class="appd_d_ch_type">
                  <!-- <p></p> -->
                  <p>{{appDetail.company_id}}</p>
                  <!-- <p class="appd_d_ch_type2"></p> -->
                  <p class="appd_d_ch_type2">{{appDetail.typeid}}</p>
                </div>
                <p class="appd_d_ch_time">2016-08-18</p>
              </div>
            </div>
            <div class="appd_detail_content_header_r fr">
              <div>
                <div class="flex flex-center">
                  <!--  -->
                  <!-- <p v-if="appDetail.status === 0" class="appd_d_dev_des"></p> -->
                  <span class="demonstration">
                    <!-- 0   1   2  -->
                    {{appDetail.status === 0 ? '' : (appDetail.status === 1 ? '' : '')}}
                  </span>
                  <div class="block appd_detail_con_status_wrap" @click="changeStatus">
                    <!-- <span class="demonstration"></span> -->
                    <el-slider v-model="appStatus" :show-tooltip="false" :max="1" :step="1" show-stops></el-slider>
                  </div>

                </div>
                <p class="appd_d_dev_status">{{(appDetail.status === 0)?'':((appDetail.status === 1)?'':'')}}</p>
              </div>
            </div>
            <!-- -->
            <img class="appd_d_qrcode" :src="require('../../assets/img/appManage/QR.png')" alt="">
          </div>

          <!--  -->
          <div class="appd_d_view">

            <div class="appd_d_v_id clearfix">
              <div class="appd_v_idwrap fl">
                <p class="appd_v_comdes">Agentld</p>
                <span class="appd_v_comval">12121254</span>
              </div>
              <div class="appd_v_edit fr" @click="editViewRange">
                <img :src="require('../../assets/img/appManage/edit.png')" alt="">
              </div>
            </div>
            <div class="appd_v_secret">
              <p class="appd_v_comdes">Secret</p>
              <span class="appd_v_comval">BFHIGJKJAKSJFKSJKFJKFJSKFJKDJKFJHF</span>
            </div>
            <div class="appd_v_view_person">
              <p class="appd_v_comdes"></p>

              <div class="appd_v_person_list">
                <div class="addp_v_person_item" v-for="item in appDetail.allow_ranges" :key="item._id">
                  <img v-if="item.type === 'user'" :src="require('../../assets/img/appManage/man.png')" alt="">
                  <img v-if="item.type === 'dep'" :src="require('../../assets/img/appManage/department.png')" alt="">
                  <span>{{item.name}}</span>
                </div>
                <!-- <div class="addp_v_person_item">
                  <img :src="require('../../assets/img/appManage/man.png')" alt="">
                  <span></span>
                </div>
                <div class="addp_v_person_item">
                  <img :src="require('../../assets/img/appManage/man.png')" alt="">
                  <span></span>
                </div> -->
              </div>
            </div>

          </div>

          <!--  -->
          <div class="addp_cdes">
            <div class="appd_cdeswrap">
              <p class="addp_cdes_title"></p>
              <div class="appd_cdes_edit" @click="clickUpdateAppDes">
                <img :src="require('../../assets/img/appManage/edit.png')" alt="">
              </div>
            </div>
            <p class="addp_cdes_des1">{{appDetail.description}}</p>
            <!-- <p class="addp_cdes_des1"></p> -->
            <!-- <p class="addp_cdes_des1">bucket</p> -->
          </div>

          <!--  -->
          <div class="addp_c_pview" v-if="appDetail.previews.length !== 0">
            <p class="addp_c_pview_title"></p>
            <div class="addp_c_pview_list" v-if="appDetail.previews">

              <div class="addp_c_pview_item" v-for="(item, index) in appDetail.previews" :key="index">
                <img :src="item" alt="">
              </div>

              <!-- <div class="addp_c_pview_item">
              <img src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAIEAAACBCAMAAADQfiliAAAAA1BMVEWJz/C4QSRIAAAAJklEQVR4nO3BAQEAAACCIP+vbkhAAQAAAAAAAAAAAAAAAAAAALwbQYIAAVCVNwcAAAAASUVORK5CYII=" alt="">
            </div>
            <div class="addp_c_pview_item">
              <img src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAIEAAACBCAMAAADQfiliAAAAA1BMVEWJz/C4QSRIAAAAJklEQVR4nO3BAQEAAACCIP+vbkhAAQAAAAAAAAAAAAAAAAAAALwbQYIAAVCVNwcAAAAASUVORK5CYII=" alt="">
            </div> -->
            </div>
          </div>

          <!-- -->
          <div class="appd_version" v-show="appDetail.platform_alias === 'H5'">
            <p class="appd_version_title"></p>
            <div class="appd_version_set">

              <div class="appd_version_set_item clearfix">
                <div class="appd_version_set_item_l fl">
                  <img :src="require('../../assets/img/appManage/pt1.png')" alt="">

                  <div class="appd_version_set_item_l_con">
                    <p class="appd_version_set_item_r_title"></p>
                    <p class="appd_version_set_item_r_des"></p>
                  </div>
                </div>
                <div class="appd_version_set_item_r fr" @click="setAppIndexUrl">
                  <p></p>
                  <img :src="require('../../assets/img/appManage/go.png')" alt="">
                </div>
              </div>

              <div class="appd_version_set_item clearfix">
                <div class="appd_version_set_item_l fl">
                  <img :src="require('../../assets/img/appManage/pt1.png')" alt="">

                  <div class="appd_version_set_item_l_con">
                    <p class="appd_version_set_item_r_title"></p>
                    <p class="appd_version_set_item_r_des"></p>
                  </div>
                </div>
                <div class="appd_version_set_item_r fr" @click="handleSetAppSDKUrl">
                  <p></p>
                  <img :src="require('../../assets/img/appManage/go.png')" alt="">
                </div>
              </div>

            </div>
          </div>

          <!--  -->
          <div class="app_detail_version" v-if="appDetail.status !== 0">
            <p class="appd_version_title"></p>
            <div class="app_detail_list">
              <div class="app_detail_list_item">
                <p class="app_detail_list_item_des"></p>
                <div>
                  <p>0.0.4(Build)</p>
                </div>
              </div>
              <div class="app_detail_list_item">
                <p class="app_detail_list_item_des"></p>
                <div>
                  <p>2019-04-25 16:13:29</p>
                </div>
              </div>
              <div class="app_detail_list_item">
                <p class="app_detail_list_item_des"></p>
                <div>
                  <p>0.0.4(Build)</p>
                </div>
              </div>
              <div class="app_detail_list_item">
                <p class="app_detail_list_item_des"></p>
                <div>
                  <p>0.0.4(Build)</p>
                  <p>0.0.4(Build)</p>
                </div>
              </div>
            </div>
          </div>

        </div>

      </div>
    </div>



    <div class="app_detail_notfind" v-if="response.isResponse && response.error === true">
      <img :src="require('../../assets/img/appManage/no2.png')" alt="">
    </div>

    <!--  -->
    <UpdateAppInfo :dialogVisible="updateAppinfo" @submit="submitUpdateAppinfo" />

    <!--  -->
    <updateAppDes :dialogVisible="updateAppDes" @submit="submitUpdateAppDes" />

    <!--  -->
    <updateSetAppIndex :dialogVisible="setAppIndex" @submit="submitSetAppIndex" />

    <!-- SDK -->
    <updateSetAppSDk :dialogVisible="setAppSDKUrl" @submit="submitSetAppSDK" />

    <!--  -->
    <viewRange @allow_ranges="allow_ranges" :alreadySelect="setViewRange.powerList" />


    <el-button type="primary" @click="installApp"></el-button>
    
  </div>
</template>
<script>
import UpdateAppInfo from './detailComponents/updateAppinfo'
import updateAppDes from './detailComponents/updateAppdes'
import updateSetAppIndex from './detailComponents/updateAppIndex'
import updateSetAppSDk from './detailComponents/updateAppSDK'
import viewRange from './components/viewRange' // 
import myMixinFn from './mixinFn'
export default {
  mixins: [myMixinFn],
  data() {
    return {
      appStatus: '',

      appid: '',
      appDetail: {
        previews: []
      },

      response: {
        isResponse: false, // 
        error: "", //    true  false
      },

      // 
      updateAppinfo: {
        dialogVisible: false, // 
        imgFile: null, //logo // 
        logoUrl: '', // logoUrl
        appName: '', // ,
        typeid: '', // id 
        isUpload: false, // 
        loadding: false, // loading
      },

      // 
      updateAppDes: {
        description: '', // 
        dialogVisible: false, // 
        isUpload: false, // 
        loadding: false, // loading
      },

      // 
      setAppIndex: {
        dialogVisible: false, // 
        isUpload: false, // 
        loadding: false, // loading
        url: ''
      },

      //  SDK 
      setAppSDKUrl: {
        dialogVisible: false, // 
        isUpload: false, // 
        loadding: false, // loading
        url: ''
      },

      // 
      setViewRange: {
        // dialogVisible: false, // 
        powerList: []
      },
    }
  },

  methods: {


    // 
    installApp(){
      console.log('');
      console.log(this.appDetail);
      var href = `itms-services://?action=download-manifest&url=${'https://wx.zplus.top'}/zero-box/application/${this.appDetail._id}_${this.appDetail.version[0].version}.plist`;
      console.log(href);
      // window.location.href = `itms-services://?action=download-manifest&url=${'http://120.79.228.70'}/zero-box/application/${this.appDetail._id}_${this.appDetail.version[0].version}.plist`;;
      window.location.href = `${'http://120.79.228.70'}/zero-box/application/${this.appDetail._id}_${this.appDetail.version[0].version}.plist`;
    },
    
    // 
    editViewRange(){
      console.log('');
    },

    // 
    allow_ranges(){
      console.log('');
    },

    // 
    async changeStatus() {
      //  status 0   1 2 
      let enableStatus = this.appDetail.status;
      let company_id = this.$zero.getCompany_id();
      let user_id = this.$zero.getUserid();

      //   
      if (enableStatus.status === 1) {
        this.appStatus = 1;
      } else if (enableStatus.status === 0 || enableStatus === 2) {
        this.appStatus = 0;
      }

      if (enableStatus === 0) {
        // 
        this.$zero.ToastWarning('');
      } else if (enableStatus === 1) {
        //   
        // 
        console.log('');
        // this.appDetail.status = 0;
        try {
          let changeResult = await this.$api.application.stopAppByid({
            company_id,
            user_id,
            id: this.appDetail._id,
          });
          console.log('');
          console.log(changeResult)
          if (changeResult.statusCode === 75500) {
            return this.$zero.ToastError('');
          }

          if (changeResult.statusCode !== 75200) {
            return this.$zero.ToastError(`:${changeResult.statusCode}`);
          }
          // 
          this.appStatus = 0;
          this.appDetail.status = 2;
          this.$zero.ToastSuccess('');
        } catch (error) {
          console.log(error);
          return this.$zero.ToastError('');
        }
      } else if (enableStatus === 2) {
        //  
        this.$zero.ToastWarning('');
      }
    },

    // SDK
    handleSetAppSDKUrl() {
      console.log('SDK');
      //   url
      this.init_appInfo(this.appDetail);
      this.setAppSDKUrl.dialogVisible = true;
    },

    //SDK
    async submitSetAppSDK() {
      console.log("");
      console.log(this.setAppSDKUrl);
      console.log(this.setAppSDKUrl.url);
      var url = this.setAppSDKUrl.url;
      if (!this.$zero.resHTTPStr(url)) {
        this.closeUpdataAppSDK(true);
        return this.$zero.ToastError('');
      }

      let company_id = this.$zero.getCompany_id();
      let user_id = this.$zero.getUserid();

      try {
        let changeResult = await this.$api.application.setRedirectDomain({
          company_id: company_id,
          user_id: user_id,
          appid: this.appDetail._id,
          redirect_domain: url
        });
        this.closeUpdataAppSDK(true);

        if (changeResult.statusCode === 75500) {
          return this.$zero.ToastError('');
        } else if (changeResult.statusCode === 75402) {
          return this.$zero.ToastError('redirect_domain ');
        }
        if (changeResult.statusCode !== 75200) {
          return this.$zero.ToastError(`${changeResult.statusCode}`);
        }
        // 
        this.closeUpdataAppSDK();
        this.$zero.ToastSuccess('');
        this.appDetail.h5.redirect_domain = url;

      } catch (err) {
        this.closeUpdataAppSDK(true);
        console.log(err);
        this.$zero.ToastError('url');
      }
    },

    closeUpdataAppSDK(isnoClose = false) {
      this.setAppSDKUrl.dialogVisible = isnoClose;
      this.setAppSDKUrl.isUpload = false;
      this.setAppSDKUrl.loadding = false;
    },

    // 
    setAppIndexUrl() {
      console.log('');
      //   url
      this.init_appInfo(this.appDetail);
      this.setAppIndex.dialogVisible = true;
    },

    // 
    async submitSetAppIndex() {
      console.log('');
      console.log(this.setAppIndex);
      let { url } = this.setAppIndex;

      if (!this.$zero.resHTTPStr(url)) {
        return this.$zero.ToastError('');
      }

      let company_id = this.$zero.getCompany_id();
      let user_id = this.$zero.getUserid();

      try {
        let changeResult = await this.$api.application.setAppHome({
          company_id,
          user_id,
          appid: this.appDetail._id,
          home: url
        });
        this.closeSetAppIndex(true);
        console.log('');

        if (changeResult.statusCode === 75500) {
          return this.$zero.ToastError('');
        } else if (changeResult.statusCode === 75402) {
          return this.$zero.ToastError('home_url ');
        }
        if (changeResult.statusCode !== 75200) {
          return this.$zero.ToastError(`:${changeResult.statusCode}`);
        }
        // 
        this.closeSetAppIndex();
        this.$zero.ToastSuccess('');
        this.appDetail.home_url = url;
      } catch (error) {
        this.closeSetAppIndex(true);
        this.$zero.ToastError('');
        console.log(error);
      }
    },

    //     true
    closeSetAppIndex(close = false) {
      // 
      this.setAppIndex.dialogVisible = close;
      this.setAppIndex.isUpload = false;
      this.setAppIndex.loadding = false;
    },

    // 
    clickUpdateAppDes() {
      console.log('');
      //  
      this.init_appInfo(this.appDetail);
      this.updateAppDes.dialogVisible = true;

    },


    // 
    async submitUpdateAppDes() {
      console.log('');
      if (this.appDetail.description === this.updateAppDes.description) {
        this.closeUpdataAppDes();
        return;
      }

      let company_id = this.$zero.getCompany_id();
      let user_id = this.$zero.getUserid();

      //  
      let updateResult = await this.$api.application.updateAppInfo({
        company_id: company_id,
        id: this.appDetail._id,
        user_id: user_id,
        data: {
          description: this.updateAppDes.description
        }
      })
        .catch(err => {
          console.log(err);
          this.closeUpdataAppDes();
          return this.$zero.ToastError('');
        })
      this.closeUpdataAppDes();

      if (updateResult.statusCode === 75500) {
        return this.$zero.ToastError('');
      }

      if (updateResult.statusCode !== 75200) {
        return this.$zero.ToastError(`:${updateResult.statusCode}`);
      }
      // 
      this.$zero.ToastSuccess('');

      // 
      this.appDetail.description = this.updateAppDes.description;
    },

    closeUpdataAppDes() {
      this.updateAppDes.dialogVisible = false;
      this.updateAppDes.isUpload = false;
      this.updateAppDes.loadding = false;
    },


    /*  */
    editAppInfo() {
      console.log('');
      //  
      this.init_appInfo(this.appDetail);
      this.updateAppinfo.dialogVisible = true;
    },

    /**
     * 
     */
    async submitUpdateAppinfo() {
      console.log('');
      console.log(this.updateAppinfo);

      // logo  
      let logoUrlResult = null;
      if (this.updateAppinfo.imgFile) {
        logoUrlResult = await this.mixinUploadImg([this.updateAppinfo.imgFile])
          .catch(err => {
            console.log(err);
            this.closeUpdataAppInfo();
            return;
          })
      }

      var isUpload = false; // 
      //  logo 
      let updateInfo = {};

      // 
      if (logoUrlResult) {
        isUpload = true;
        updateInfo.icon = logoUrlResult[0].file;
      }
      if (this.updateAppinfo.appName !== this.appDetail._name) {
        isUpload = true;
        updateInfo._name = this.updateAppinfo.appName;
      }
      if (this.updateAppinfo.typeid !== this.appDetail.typeid) {
        isUpload = true;
        updateInfo.typeid = this.updateAppinfo.typeid;
      }
      /*
          
        {
          _name: "",
          icon: "http://4545454.1.png",
          typeid: "5d5289ed851e1751782e087e"
        }
      */
      console.log('11111');
      if (!isUpload) {
        this.closeUpdataAppInfo();
        return;
      }

      console.log('');


      let company_id = this.$zero.getCompany_id();
      let user_id = this.$zero.getUserid();


      //  
      let updateResult = await this.$api.application.updateAppInfo({
        company_id: company_id,
        id: this.appDetail._id,
        user_id: user_id,
        data: updateInfo
      })
        .catch(err => {
          console.log(err);
          this.closeUpdataAppInfo();
          return;
        })

      this.closeUpdataAppInfo();

      console.log('');
      console.log(updateResult);
      if (updateResult.statusCode === 75500) {
        return this.$zero.ToastError('');
      }

      if (updateResult.statusCode !== 75200) {
        return this.$zero.ToastError(`:${updateResult.statusCode}`);
      }
      // 
      this.$zero.ToastSuccess('');

      // 
      if (logoUrlResult) {
        this.appDetail.icon = logoUrlResult[0].file;
      }
      this.appDetail._name = this.updateAppinfo.appName;
      this.appDetail.typeid = this.updateAppinfo.typeid;
    },

    // 
    closeUpdataAppInfo() {
      this.updateAppinfo.isUpload = false;
      this.updateAppinfo.loadding = false;
      this.updateAppinfo.dialogVisible = false;
    },

    /**
     * 
     */
    backBtn() {
      // console.log('1111');
      window.history.go(-1);
    },

    /**
     * 
     */
    updateApp() {
      console.log('');
      //  IOS Andriod  urlid
      let { platform_alias, _id } = this.appDetail;
      console.log(platform_alias);
      if (platform_alias === 'H5') {
        // H5
        console.log('H5');
        this.$router.push('/appmanage/createH5?updateappid=' + _id);
      } else if (platform_alias === 'SMALL ') {
        // 
        console.log('');
        this.$router.push('/appmanage/createSmall?updateappid=' + _id);
      } else if (platform_alias === 'IOS') {
        // IOS
        console.log('IOS');
        this.$router.push('/appmanage/createIos?updateappid=' + _id);
      } else if (platform_alias === 'ANDROID') {
        // Andriod
        console.log('ANDROID');
        this.$router.push('/appmanage/createAndriod?updateappid=' + _id);
      } else {
        this.$zero.ToastError('');
      }

    },

    /**
     * 
     */
    async delApp() {
      let isSubmit = await this.$zero.DeleteTips(`${this.appDetail._name}`);
      if (!isSubmit) {
        return;
      }

      // 
      console.log('');
      let company_id = this.$zero.getCompany_id();
      let user_id = this.$zero.getUserid();
      let delAppResult = await this.$api.application.delAppByid({
        company_id: company_id,
        id: this.appid,
        user_id: user_id
      });
      console.log('');
      console.log(delAppResult);
      if (delAppResult.statusCode === 75500) {
        return this.$zero.ToastError('');
      }
      if (delAppResult.statusCode !== 75200) {
        return this.$zero.ToastError(`:${delAppResult.statusCode}`);
      }
      // 
      this.$zero.notify({
        title: '',
        message: `${this.appDetail._name}`,
        duration: 0
      });

      setTimeout(() => {
        this.$router.go(-1);
      }, 2000);
    },

    /**
     * 
     */
    async publishApp() {
      console.log('');
      let company_id = this.$zero.getCompany_id();
      let user_id = this.$zero.getUserid();

      let publishResult = await this.$api.application.releaseAppByid({
        company_id: company_id,
        id: this.appid,
        user_id: user_id
      });
      console.log('');
      if (publishResult.statusCode === 75500) {
        return this.$zero.ToastError('');
      }

      if (publishResult.statusCode !== 75200) {
        return this.$zero.ToastError(`${publishResult.statusCode}`)
      }
      // 
      this.$zero.ToastSuccess('');
      this.appDetail.status = 1;
      // 
      this.appStatus = 1;
    },

    /**
     * id
     * @params {String} id id
     */
    async getAppDetailByid(id) {
      console.log('id');
      console.log(id);

      // 
      let compony_id = this.$zero.getCompany_id();
      let user_id = this.$zero.getUserid();

      let appDetail = await this.$api.application.getAppDetail({
        company_id: compony_id,
        id: id,
        user_id: user_id
      });

      this.response.isResponse = true; // 

      if (appDetail.statusCode === 75500) {
        this.response.error = true;
        return this.$zero.ToastError('');
      } else if (appDetail.statusCode === 75401) {
        this.response.error = true;
        return this.$zero.ToastError('id');
      } else if (appDetail.statusCode === 75400) {
        this.response.error = true;
        //   
        return this.$zero.ToastError(`${appDetail.msg}`);
      }

      if (appDetail.statusCode !== 75200) {
        this.response.error = true;
        return this.$zero.ToastError(`:${appDetail.statusCode}`);
      }

      this.response.error = false; // 

      // 
      // console.log('');
      // console.log(appDetail);
      this.appDetail = appDetail.app;
      // 
      this.init_appInfo(appDetail.app);
    },

    // 
    init_appInfo(appDetail) {
      console.log('');

      // 
      this.updateAppinfo.logoUrl = appDetail.icon;
      this.updateAppinfo.appName = appDetail._name;
      this.updateAppinfo.typeid = appDetail.typeid;

      // 
      this.updateAppDes.description = appDetail.description;

      // h5
      this.setAppSDKUrl.url = appDetail.h5.redirect_domain;

      // 
      this.setAppIndex.url = appDetail.home_url;

      // 
      if (appDetail.status === 1) {
        // 
        this.appStatus = 1;
      } else if (appDetail.status === 0 || appDetail.status === 2) {
        //  | 
        this.appStatus = 0;
      }

      // 
      this.imgFile = null;
    }
  },

  created() {
    let { appid } = this.$route.query;
    console.log('id');
    console.log(appid);

    this.appid = appid;
    this.getAppDetailByid(appid);
  },
  components: {
    UpdateAppInfo,
    updateAppDes,
    updateSetAppIndex,
    updateSetAppSDk,
    viewRange
  }
}
</script>
